/* src/components/ReservationGrid.css */
/* VERSION MODERNE - 630px HAUTEUR EXACTE - SANS SCROLL HORIZONTAL */

.reservation-grid-container {
  background: white;
  border-radius: 20px;
  padding: 1.5rem;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.15);
  animation: slideIn 0.5s ease;
}

@keyframes slideIn {
  from { opacity: 0; transform: translateX(-50px); }
  to { opacity: 1; transform: translateX(0); }
}

.grid-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  padding-bottom: 0.8rem;
  border-bottom: 2px solid #e0e0e0;
}

.grid-header h2 {
  color: #667eea;
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0;
}

.back-button {
  background: linear-gradient(135deg, #6b7280 0%, #4b5563 100%);
  color: white;
  border: none;
  padding: 0.7rem 1.5rem;
  border-radius: 12px;
  font-size: 0.95rem;
  font-weight: 600;
  transition: all 0.3s ease;
  cursor: pointer;
  box-shadow: 0 4px 12px rgba(107, 114, 128, 0.3);
}

.back-button:hover {
  background: linear-gradient(135deg, #4b5563 0%, #374151 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(107, 114, 128, 0.4);
}

/* Navigation semaine - STYLE BOUTONS RESTAURÉ */
.week-navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
  padding: 1rem;
  background: linear-gradient(135deg, #e3f2fd 0%, #bbdefb 100%);
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(33, 150, 243, 0.15);
}

.week-navigation button,
.week-navigation .nav-button {
  background: linear-gradient(135deg, #2196f3 0%, #1976d2 100%);
  color: white;
  border: none;
  padding: 0.7rem 1.2rem;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 3px 10px rgba(33, 150, 243, 0.3);
}

.week-navigation button:hover:not(:disabled),
.week-navigation .nav-button:hover {
  background: linear-gradient(135deg, #1976d2 0%, #1565c0 100%);
  transform: translateY(-2px) scale(1.03);
  box-shadow: 0 5px 15px rgba(33, 150, 243, 0.4);
}

.week-navigation button:disabled {
  background: #cbd5e1;
  cursor: not-allowed;
  box-shadow: none;
}

.week-navigation .current-date {
  background: linear-gradient(135deg, #2196f3 0%, #1976d2 100%);
  color: white;
  padding: 0.7rem 1.5rem;
  border-radius: 10px;
  font-weight: 700;
  font-size: 1.1rem;
  box-shadow: 0 3px 10px rgba(33, 150, 243, 0.3);
}

/* Instructions */
.grid-instructions {
  background: linear-gradient(135deg, #dbeafe 0%, #bfdbfe 100%);
  padding: 0.7rem 1rem;
  border-radius: 10px;
  margin-bottom: 1rem;
  border-left: 4px solid #3b82f6;
  box-shadow: 0 2px 6px rgba(59, 130, 246, 0.15);
}

.grid-instructions p {
  margin: 0;
  color: #1e40af;
  font-weight: 500;
  font-size: 0.85rem;
}

.mobile-instruction {
  display: none;
  background: linear-gradient(135deg, #fef3c7 0%, #fde68a 100%);
  padding: 0.7rem 1rem;
  border-radius: 10px;
  margin-bottom: 1rem;
  border-left: 4px solid #f59e0b;
  color: #92400e;
  font-weight: 500;
  font-size: 0.85rem;
}

/* Container principal - DEUX COLONNES */
.reservation-content {
  display: grid;
  grid-template-columns: 1fr 400px;
  gap: 1.5rem;
  align-items: start;
}

.grid-column {
  width: 100%;
  height: 630px;
  overflow: hidden;
  border-radius: 14px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.form-column {
  width: 100%;
  height: 630px;
  max-height: 630px;
  overflow: hidden;
}

/* GRILLE DE RÉSERVATION - 630px EXACTEMENT - BLEU TRÈS CLAIR */
.reservation-grid {
  display: grid;
  grid-template-columns: 60px repeat(9, 1fr);
  column-gap: 2px;
  row-gap: 1px;
  padding: 0.8rem;
  background: linear-gradient(135deg, #93c5fd 0%, #bfdbfe 100%);
  border-radius: 14px;
  height: 630px;
  width: 100%;
  overflow: hidden;
}

/* Header de colonne (nom des salles) - GRIS ARDOISE */
.salle-header {
  background: linear-gradient(135deg, #64748b 0%, #475569 100%);
  color: white;
  padding: 0.4rem 0.2rem;
  text-align: center;
  font-weight: 700;
  font-size: 0.6rem;
  line-height: 1.1;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(100, 116, 139, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
  gap: 0.1rem;
  overflow: hidden;
}

.salle-header .salle-name {
  display: block;
  font-size: 0.6rem;
  font-weight: 700;
}

.salle-header .salle-capacity {
  display: block;
  font-size: 0.55rem;
  font-weight: 500;
  opacity: 0.9;
}

.salle-header:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

/* Header d'heure - GRIS ARDOISE */
.time-label {
  background: linear-gradient(135deg, #64748b 0%, #475569 100%);
  color: white;
  padding: 0.4rem 0.2rem;
  text-align: center;
  font-weight: 700;
  font-size: 0.75rem;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(100, 116, 139, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.time-label:hover {
  transform: scale(1.03);
  box-shadow: 0 3px 10px rgba(59, 130, 246, 0.4);
}

/* CRÉNEAUX - TUILES MODERNES SANS BORDS */
.time-slot {
  background: white;
  min-height: 35px;
  max-height: 35px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  touch-action: none;
  border-radius: 8px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.08);
  position: relative;
  overflow: hidden;
  font-size: 0.7rem;
  color: #64748b;
  font-weight: 600;
  white-space: nowrap;
  text-overflow: ellipsis;
}

/* Effet de brillance au survol */
.time-slot::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.4) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.time-slot:hover::before {
  opacity: 1;
}

.time-slot:hover:not(.reserved):not(.lunch-break) {
  background: linear-gradient(135deg, #dbeafe 0%, #bfdbfe 100%);
  transform: scale(1.1) translateY(-2px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
  z-index: 10;
}

/* Créneau sélectionné - VERT MODERNE */
.time-slot.selected {
  background: linear-gradient(135deg, #10b981 0%, #34d399 100%);
  color: white;
  font-weight: 700;
  transform: scale(1.08);
  box-shadow: 0 4px 16px rgba(16, 185, 129, 0.4);
  animation: pulse 0.5s ease;
  z-index: 20;
}

@keyframes pulse {
  0%, 100% { transform: scale(1.08); }
  50% { transform: scale(1.12); }
}

/* Créneau réservé - GARDE COULEUR DE FOND */
.time-slot.reserved {
  cursor: default;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.12);
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.6rem;
  font-weight: 700;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0 0.2rem;
}

.time-slot.reserved:hover {
  transform: scale(1.03);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.15);
}

/* Pause déjeuner - ORANGE MODERNE */
.time-slot.lunch-break:not(.reserved) {
  background: linear-gradient(135deg, #fef3c7 0%, #fde68a 100%);
  color: #92400e;
  font-weight: 600;
}

.time-slot.lunch-break:hover:not(.reserved) {
  background: linear-gradient(135deg, #fde68a 0%, #fcd34d 100%);
  transform: scale(1.08);
  box-shadow: 0 4px 12px rgba(245, 158, 11, 0.3);
}

.time-slot.lunch-break.selected {
  background: linear-gradient(135deg, #10b981 0%, #34d399 100%);
}

/* Salles admin verrouillées - ROUGE MODERNE SANS CADENAS */
.time-slot.admin-only-locked {
  background: linear-gradient(135deg, #fee2e2 0%, #fecaca 100%);
  cursor: not-allowed;
  position: relative;
  color: #991b1b;
}

/* Cacher le cadenas dans les tuiles normales */
.time-slot .lock-icon {
  display: none;
}

.time-slot.admin-only-locked:hover {
  background: linear-gradient(135deg, #fecaca 0%, #fca5a5 100%);
  transform: scale(1.05);
  box-shadow: 0 3px 10px rgba(239, 68, 68, 0.3);
}

/* Formulaire de réservation */
.reservation-form {
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  padding: 1.2rem;
  border-radius: 14px;
  box-shadow: 0 6px 24px rgba(0, 0, 0, 0.12);
  border: 2px solid #e2e8f0;
  height: 630px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

/* Scrollbar personnalisée */
.reservation-form::-webkit-scrollbar {
  width: 6px;
}

.reservation-form::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 10px;
}

.reservation-form::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #667eea 0%, #764ba2 100%);
  border-radius: 10px;
}

.reservation-form h3 {
  color: #667eea;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
  flex-shrink: 0;
}

.form-title-line1 {
  font-size: 1.2rem;
  font-weight: 700;
}

.form-title-line2 {
  font-size: 0.85rem;
  color: #64748b;
  font-weight: 500;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.4rem;
  color: #334155;
  font-weight: 600;
  font-size: 0.85rem;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 0.6rem 0.8rem;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 0.85rem;
  transition: all 0.3s ease;
  background: white;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.15);
}

/* Boutons d'action */
.form-actions {
  display: flex;
  gap: 0.8rem;
  margin-top: auto;
  padding-top: 1rem;
  flex-shrink: 0;
}

.cancel-button {
  flex: 1;
  background: linear-gradient(135deg, #94a3b8 0%, #64748b 100%);
  color: white;
  border: none;
  padding: 0.7rem 1.2rem;
  border-radius: 10px;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 3px 10px rgba(148, 163, 184, 0.3);
}

.cancel-button:hover {
  background: linear-gradient(135deg, #64748b 0%, #475569 100%);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(148, 163, 184, 0.4);
}

.submit-button {
  flex: 2;
  background: linear-gradient(135deg, #10b981 0%, #34d399 100%);
  color: white;
  border: none;
  padding: 0.7rem 1.2rem;
  border-radius: 10px;
  font-size: 0.9rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 3px 10px rgba(16, 185, 129, 0.3);
}

.submit-button:hover:not(:disabled) {
  background: linear-gradient(135deg, #34d399 0%, #6ee7b7 100%);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(16, 185, 129, 0.4);
}

.submit-button:disabled {
  background: #cbd5e1;
  cursor: not-allowed;
  box-shadow: none;
}

/* Warning message */
.warning-message {
  background: linear-gradient(135deg, #fef3c7 0%, #fde68a 100%);
  padding: 0.8rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  border-left: 4px solid #f59e0b;
  color: #92400e;
  font-size: 0.85rem;
}

/* Modal de succès */
.success-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.success-modal {
  background: white;
  padding: 2rem;
  border-radius: 16px;
  max-width: 550px;
  width: 90%;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  animation: slideUp 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.success-modal-header {
  text-align: center;
  margin-bottom: 1.5rem;
}

.success-icon {
  font-size: 3.5rem;
  display: block;
  margin-bottom: 0.8rem;
  animation: bounce 0.6s ease;
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-15px); }
}

.success-modal h2 {
  color: #10b981;
  font-size: 1.5rem;
  margin: 0;
}

/* Responsive */
@media (max-width: 1200px) {
  .reservation-content {
    grid-template-columns: 1fr;
  }
  
  .form-column {
    height: auto;
    max-height: none;
  }
  
  .grid-column {
    height: auto;
  }
  
  .reservation-grid {
    height: auto;
  }
}

@media (max-width: 768px) {
  .grid-instructions {
    display: none;
  }
  
  .mobile-instruction {
    display: block;
  }
  
  .reservation-grid {
    overflow-x: auto;
  }
}

/* 4. MES RÉSERVATIONS - SANS SCROLL HORIZONTAL */
.my-reservations-container {
  max-width: 100%;
  overflow-x: hidden;
}

.my-reservations-container .table-container {
  overflow-x: auto;
  max-width: 100%;
}

.my-reservations-container .reservations-table {
  width: 100%;
  min-width: 800px;
}
